<template>
  <div>
    <h1>{{ isEditMode ? 'Edit Person' : 'Add Person' }}</h1>
    <form @submit.prevent="submitForm">
      <div>
        <label for="firstName">First Name:</label>
        <input type="text" id="firstName" v-model="person.firstName" required />
      </div>
      <div>
        <label for="lastName">Last Name:</label>
        <input type="text" id="lastName" v-model="person.lastName" required />
      </div>
      <div>
        <label for="email">Email:</label>
        <input type="email" id="email" v-model="person.email" required />
      </div>
      <div v-if="errorMessage" class="error">{{ errorMessage }}</div>
      <button type="submit">{{ isEditMode ? 'Update' : 'Create' }}</button>
    </form>
  </div>
</template>

<script>
import axios from 'axios';
import { ref, reactive, onMounted } from 'vue';
import { useRoute, useRouter } from 'vue-router';

export default {
  name: 'PersonForm',
  setup() {
    const route = useRoute();
    const router = useRouter();
    const person = reactive({
      firstName: '',
      lastName: '',
      email: ''
    });
    const isEditMode = ref(false);
    const errorMessage = ref('');

    const fetchPerson = async (id) => {
      try {
        const response = await axios.get(`http://localhost:8081/api/persons/${id}`);
        Object.assign(person, response.data);
      } catch (error) {
        console.error('Error fetching person:', error);
        errorMessage.value = 'Error fetching person data.';
      }
    };

    const submitForm = async () => {
      try {
        if (isEditMode.value) {
          await axios.put(`http://localhost:8081/api/persons/${route.params.id}`, person);
        } else {
          await axios.post('http://localhost:8081/api/persons', person);
        }
        router.push('/');
      } catch (error) {
        console.error('Error submitting form:', error);
        errorMessage.value = 'Error submitting form.';
      }
    };

    onMounted(() => {
      if (route.params.id) {
        isEditMode.value = true;
        fetchPerson(route.params.id);
      }
    });

    return {
      person,
      isEditMode,
      errorMessage,
      submitForm
    };
  }
};
</script>

<style scoped>
.error {
  color: red;
}
</style>
