<template>
  <div>
    <h1>Person List</h1>
    <table>
      <thead>
        <tr>
          <th>ID</th>
          <th>First Name</th>
          <th>Last Name</th>
          <th>Email</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="person in persons" :key="person.id">
          <td>{{ person.id }}</td>
          <td>{{ person.firstName }}</td>
          <td>{{ person.lastName }}</td>
          <td>{{ person.email }}</td>
          <td>
            <button @click="editPerson(person.id)">Edit</button>
            <button @click="deletePerson(person.id)">Delete</button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import axios from 'axios';
import { ref, onMounted } from 'vue';
import { useRouter } from 'vue-router';

export default {
  name: 'PersonList',
  setup() {
    const persons = ref([]);
    const router = useRouter();

    const fetchPersons = async () => {
      try {
        const response = await axios.get('http://localhost:8081/api/persons');
        persons.value = response.data;
      } catch (error) {
        console.error('Error fetching persons:', error);
      }
    };

    const editPerson = (id) => {
      router.push(`/person/${id}`);
    };

    const deletePerson = async (id) => {
      try {
        await axios.delete(`http://localhost:8081/api/persons/${id}`);
        fetchPersons();
      } catch (error) {
        console.error('Error deleting person:', error);
      }
    };

    onMounted(fetchPersons);

    return {
      persons,
      editPerson,
      deletePerson
    };
  }
};
</script>

<style scoped>
table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
}

th {
  background-color: #f2f2f2;
}
</style>
